import { ArrowDownwardOutlined, ArrowUpwardOutlined, ChatBubbleOutline, Close, MoreHorizOutlined, RepeatOneOutlined, ShareOutlined } from '@mui/icons-material';
import { Avatar } from '@mui/material';
import React,{ useState } from 'react'
import "./css/Post.css";
import Modal from "react-responsive-modal";
import "react-responsive-modal/styles.css";
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css';
export default function Post() {
    const [isModalOpen, setIsModalOpen] = useState(false);
    const Closefunc = <Close/>;
  return (
    <div className="post">
        <div className="post__info">
            <Avatar/>
            <h4>User Name</h4>
            <small>Timestamp</small>
        </div>
        <div className="post__body">
        <div className="post__question">
        <p>THis is test question</p>
            <button className="post__btnAnswer" onClick={() => 
              setIsModalOpen(true)}>Answers</button>
              <Modal open={isModalOpen} close={Closefunc} onClose={() => setIsModalOpen(false)} closeOnEsc center closeOnOverlayClick={false}
            styles={{
              overlay: {
                height: "auto",
              },
            }}
          ><div className="modal_question">
            <h2>This is test question</h2>
            <p>asked  by  <span className='name'>Username  </span>on {''}T<span className='name'>Timestamp</span></p>
          </div>
          <div className="modal_answer">
            <ReactQuill placeholder='Enter your answer'/>
          </div>
          <div className="modal__button">
          <button className="cancle" onClick={() => setIsModalOpen(false)}>Cancel</button>
          <button  type="submit" className="add">Add Question</button>
          </div>
          </Modal>
        </div>
            
        </div>
        <div className="post__footer">
        <div className="post__footerAction">
            <ArrowUpwardOutlined/>
            <ArrowDownwardOutlined/>
        </div>
        <RepeatOneOutlined/>
        <ChatBubbleOutline/>
        <div className="post__footerLeft">
            <ShareOutlined/>
            <MoreHorizOutlined/>
        </div>
        </div>
        <p style={{
          color: "rgba(0,0,0,0.5)",
          fontSize: "12px",
          fontWeight: "bold",
          margin: "10px 0",
        }}>1 Answer</p>
        <div className="post_answer" style={{
          margin: "5px 0px 0px 0px ",
          padding: "5px 0px 0px 20px",
          borderTop: "1px solid lightgray",
        }}>
            <div className="post-answer-container" style={{
                display: "flex",
                flexDirection: "column",
                width: "100%",
                padding: "10px 5px",
                borderTop: "1px solid lightgray",
              }}>
                <div className="post-answered" style={{
                alignItems:"center",
                display: "flex",
                marginBottom: "10px",
                  fontSize: "12px",
                  fontWeight: 600,
                  color: "#888",}}>
                    <Avatar/>
                    <div className="post-info"  style={{
                    margin: "0px 10px",
                  }}>
                        <p>User name</p>
                        <span>Timestamp</span>
                    </div>
                </div>
                <div className="post-answer">
                THis is test answer
                </div>
            </div>
        </div>
    </div>
  )
}
